$black: #2a2a24;
$darkblue: #1a1981;
$lightblue: #9c9bcf;
$bodyBackground: #f1f4f8;

* {
  box-sizing: border-box;
}

html,
body,
#root {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  overflow: hidden;
  font-size: 14px;
}

.canvas-wrapper {
  width: 100%;
  height: 90vh;
  position: relative;
}


body {
  font-family: -apple-system, BlinkMacSystemFont, avenir next, avenir,
    helvetica neue, helvetica, ubuntu, roboto, noto, segoe ui, arial, sans-serif;
  background: $bodyBackground;
}


header {
  height: 10vh;
  width: 100%;
  padding: 24px 48px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  color: $black;
  .logo,
  .episode {
    span {
      font-size: 1rem;
      font-weight: 700;
      letter-spacing: 0.04rem;
      color: $darkblue;
    }
  }
}

canvas {
  outline: none !important;
  border: none !important;
} 

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}
label, .labelRotate {
  color:$darkblue;
}


input[type=number] {
  -moz-appearance: textfield;
  width: 35px;
  color: $darkblue;
  outline: none;
  border: $lightblue 2px solid;
  background-color: white;
  border-radius: 5px;
  font-weight: 600;
  &:hover, &:focus {
    border-color: $darkblue;
  }
  &::-webkit-input-placeholder{
    color: $lightblue;
  }
  &::placeholder {
    color: $lightblue;
  }
}



.switch {
  position: relative;
  display: inline-block;
  width: 40px;
  height: 22px;
  input {
    opacity: 0;
    width: 0;
    height: 0;
  }
}



/* The slider */
.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: $lightblue;
  -webkit-transition: .4s;
  transition: .4s;
  &:before {
    position: absolute;
    content: "";
    height: 15px;
    width: 15px;
    left: 4px;
    bottom: 3px;
    background-color: white;
    -webkit-transition: .4s;
    transition: .4s;
  } 
}



input:checked + .slider {
  background-color: $darkblue;
}

input:focus + .slider {
  box-shadow: 0 0 1px $darkblue;
}

$transletChackbox: 16px;
input:checked + .slider:before {
  
  -webkit-transform: translateX($transletChackbox);
  -ms-transform: translateX($transletChackbox);
  transform: translateX($transletChackbox);
}

/* Rounded sliders */
.slider.round {
  border-radius: 34px;
  &:before {
    border-radius: 50%;
  }
}

form {
  display: flex;
  align-items: center;
  gap: .5rem;
  font-weight: 600;
}

@media (max-width: 600px){
  header {
    height: 15vh;
    padding: .6rem .8rem 0;
    .logo {
      span{
      font-size: .9rem;
      }
    }
    form {
      font-size: .8rem;
      display: grid;
      grid-template-columns: 1fr 1fr;
    }
  }

  .canvas-wrapper {
    height: 85vh;
  }
}
